@import url(https://fonts.googleapis.com/css?family=Orbitron|VT323);
body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

*,
*::after,
*::before {
  box-sizing: inherit; }

html,
body {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Orbitron", sans-serif; }

body {
  background: -webkit-linear-gradient(bottom, rgba(85, 10, 71, 0.45), rgba(72, 110, 13, 0.45)), url(/drum-machine/static/media/bckgrnd3.783c45de.jpeg);
  background: linear-gradient(0deg, rgba(85, 10, 71, 0.45), rgba(72, 110, 13, 0.45)), url(/drum-machine/static/media/bckgrnd3.783c45de.jpeg);
  background-size: cover; }

.Soundpads {
  width: 100%;
  height: 100%;
  background-color: #1eaafc;
  background-image: -webkit-linear-gradient(320deg, #6c52d9 0%, #1eaafc 85%, #3edfd7 100%);
  background-image: linear-gradient(130deg, #6c52d9 0%, #1eaafc 85%, #3edfd7 100%);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  color: #fff;
  border-radius: 4px;
  border: 1px solid #f1c40f;
  border-radius: 50%; }
  .Soundpads .drum-pad {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    outline: none; }
  .Soundpads .drum-pad p {
    cursor: default;
    text-decoration: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 1rem; }

@-webkit-keyframes bounce {
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); } }

@keyframes bounce {
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); } }

@media (max-width: 720px) {
  .Soundpads .drum-pad p {
    font-size: 2vw; } }

#drum-machine {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
  box-shadow: 0 0 0 3px gray, 0 0 0 6px #b3b3b3;
  padding: 1rem; }

#display {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 2rem;
  font-weight: 500;
  text-transform: uppercase; }
  #display p {
    font-family: "VT323", monospace;
    font-size: 2rem;
    background-color: black;
    color: white;
    width: 50%;
    height: 50px;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid #f1c40f;
    border-radius: 5px;
    overflow: hidden;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: -webkit-flex;
    display: flex; }

.Wrapper {
  display: grid;
  width: 60vmin;
  height: 60vmin;
  margin: 2vmin auto;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 1rem;
  padding: 1rem; }

@media (max-width: 720px) {
  .Wrapper {
    width: 80vmin;
    height: 80vmin; }
  #display p {
    font-size: 4vw;
    height: auto; } }

